import { ProviderService } from 'src/app/services/provider.service';
import { EnvService } from 'src/app/services/env.service';
import { UserService } from 'src/app/services/user.service';
import { RemoteStatsType } from './../../models/remote-stats';
import { MenuController, IonRefresher } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-adm-dashboard',
  templateUrl: './adm-dashboard.page.html',
  styleUrls: ['./adm-dashboard.page.scss'],
})
export class AdmDashboardPage implements OnInit {
  refresher:IonRefresher = null;
  constructor(
    private menu:MenuController,
    public userService:UserService,
    public providerService:ProviderService,
    private env:EnvService
  ) { }

  ngOnInit() {
    
    this.menu.enable(true);
    this.providerService.statsByStatusShared.getUpdateNotifier(this).subscribe(()=>{
      if(this.refresher)
      {
        this.refresher.complete();
      }
    })
    this.providerService.statsByStatusShared.request();
    this.userService.statsShared.getUpdateNotifier(this).subscribe(()=>{
      if(this.refresher)
      {
        this.refresher.complete();
      }
    })
    this.userService.statsShared.request();
  }
  refresh(event) {
    this.refresher = event.target;
    this.providerService.statsByStatusShared.request();
    this.userService.statsShared.request();
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    
  }

}
